import React, { useState, useRef, useEffect } from 'react';
import packageJson from "../../package.json"
import DatePicker, { Calendar, DateObject } from "react-multi-date-picker"
// import DatePicker from "../components/date_picker/date_picker"
// import Calendar from "../components/calendar/calendar"
// import DateObject from "react-date-object"
import Example from "../components/example"
import { ReactComponent as GitHub } from "../assets/github.svg"
import { ReactComponent as NPM } from "../assets/npm.svg"
import screenshot from "../assets/screenshot.jpg"
import "../calendar_demo.css"
import Arrow from "../components/arrow/arrow"
import { Link } from "react-router-dom"

// import Settings from "../plugins/settings/settings"
// import DatePickerHeader from "../plugins/date_picker_header/date_picker_header.js"
// import MultiColors from "../plugins/mutli_colors/multi_colors"
// import DatePanel from "../plugins/date_panel/date_panel"
// import Weekends from "../plugins/weekends/weekends"
// import Toolbar from '../plugins/toolbar/toolbar';


import { Settings, DatePickerHeader, MultiColors, DatePanel, Weekends, Toolbar } from "react-multi-date-picker/plugins"

import "../custom-class.css"

import Prism from 'prismjs';
import "prismjs/themes/prism-okaidia.css"
import 'prismjs/components/prism-jsx.min';

function Plugin() {
  const [isActive, setIsActive] = useState(false)
  const sidebarRef = useRef(null)
  const appRef = useRef(null)
  const [settings1, setSettings1] = useState({})
  const [settings2, setSettings2] = useState({ multiple: true })
  const [settings3, setSettings3] = useState({ value: new Date(), format: "MM-DD-YYYY HH:mm:ss", timePicker: true })

  const [props, setProps] = useState({ value: [new Date()], multiple: true })
  const [props2, setProps2] = useState({ multiple: true })

  const [rangeValue, setRangeValue] = useState([
    new DateObject(),
    new DateObject().add(2, "days"),
    new DateObject().add(10, "days")
  ])

  const yesterday = new DateObject().subtract(1, "day")
  const today = new DateObject()
  const tomorrow = new DateObject().add(1, "day")

  yesterday.color = "red"
  today.color = "blue"
  tomorrow.color = "red"

  const initialProps3 = { multiple: true, value: [yesterday, today, tomorrow] }

  const [props3, setProps3] = useState(initialProps3)
  const [value, setValue] = useState()
  const [value1, setValue1] = useState([])
  const [value2, setValue2] = useState([])

  const [mapDays, setMapDays] = useState()

  useEffect(() => Prism.highlightAll(), [])

  useEffect(() => {
    let scrollToTop = document.querySelector(".scroll-to-top");
    let main = document.querySelector(".main")

    document.addEventListener("click", handleClickOutside)
    scrollToTop.addEventListener("click", toTop)
    main.addEventListener("scroll", activeScrollTop)

    function handleClickOutside(event) {
      if (sidebarRef.current && !sidebarRef.current.contains(event.target) && !event.target.classList.contains("open-sidebar")) {
        setIsActive(false);
      }
    }

    function activeScrollTop() {
      scrollToTop.style.display = main.scrollTop > 50 ? "block" : "none"
    }

    function toTop() {
      main.scrollTop = 0;
    }

    return () => {
      document.removeEventListener("click", handleClickOutside, false)
      scrollToTop.removeEventListener("click", toTop)
      window.removeEventListener("scroll", activeScrollTop)
    }
  }, [])

  const logos = {
    npm: <NPM />,
    github: <GitHub />
  }

  let Doc = {
    title: "DatePicker",
    screenshot,
    navbar: {
      title: "React Multi Date Picker",
      logos: [
        { name: "github", url: "https://github.com/shahabyazdi/react-multi-date-picker" },
        { name: "npm", url: "https://www.npmjs.com/package/react-multi-date-picker" }
      ]
    },
    examples: [
      {
        title: "Usage",
        description: <div>
          <p><b style={{ color: "red" }}>WARNING:</b>The plugins section is still under development and its capabilities may change rapidly. So until the development of plugins is completed, expect a sudden change in its features.</p>
          <p>Plugins Prop accepts an array of plugins, which the following two properties can be entered on each of them.</p>
          <table>
            <thead>
              <tr>
                <th>Prop</th>
                <th>default</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>position</td>
                <td>"right"</td>
              </tr>
              <tr>
                <td>disabled</td>
                <td>false</td>
              </tr>
            </tbody>
          </table>
          <p>This is the general way to add a imported plugin to Datepicker:</p>
          <pre>
            <code className="language-jsx">
              {`import React from "react"
import DatePicker from "react-multi-date-picker"
import { MyFavoritePlugin } from "react-mylti-date-picker/plugins"

export default function Example() {
  return (
    <DatePicker
      plugins={[
        <MyFavoritePlugin />
      ]}
    />
  )
}

`}
            </code>
          </pre>
        </div>
      },
      {
        title: "Plugins",
        description: <ul>
          <li>DatePickerHeader</li>
          <li>DatePanel</li>
          <li>MultiColors</li>
          <li>Settings</li>
          <li>Weekends</li>
          <li>Toolbar</li>
        </ul>
      },
      {
        title: "Positions",
        description: <ul>
          <li>top</li>
          <li>bottom</li>
          <li>left</li>
          <li>right</li>
        </ul>
      },
      {
        title: "Examples",
        examples: [
          {
            title: "All In One",
            code: `import { 
  Settings, 
  DatePickerHeader,
  MultiColors, 
  DatePanel,
  Toolbar
} from "react-multi-date-picker/plugins"

const initialProps = { value: [new Date()], multiple: true }
const [props, setProps] = useState(initialProps)
.
.
.
<div style={{ display: "flex", justifyContent: "center" }}>
  <Calendar
    {...props}
    plugins={[
      <MultiColors position="left" setProps={setProps} />,
      <DatePickerHeader position="top" size="medium" />,
      <Settings position="bottom" setProps={setProps} defaultActive="locale" />,
      <DatePanel position="right" sort="date" eachDaysInRange />,
      <Toolbar position="bottom" />
    ]}
  />
</div>`,
            jsx: <div style={{ display: "flex", justifyContent: "center" }}>
              <Calendar
                {...props}
                plugins={[
                  <MultiColors position="left" setProps={setProps} />,
                  <DatePickerHeader position="top" size="medium" />,
                  <Settings position="bottom" setProps={setProps} defaultActive="locale" />,
                  <DatePanel position="right" sort="date" eachDaysInRange />,
                  <Toolbar position="bottom" />
                ]}
              />
            </div>
          },
          {
            title: "Header",
            description: <div>
              <h5>Header Props:</h5>
              <table style={{ marginLeft: "15px" }}>
                <thead>
                  <tr>
                    <th>Prop</th>
                    <th>Type</th>
                    <th>Default</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td>size</td>
                    <td>String</td>
                    <td>"big"</td>
                  </tr>
                  <tr>
                    <td>calendar</td>
                    <td>String</td>
                    <td>Default DatePicker Calendar</td>
                  </tr>
                  <tr>
                    <td>locale</td>
                    <td>String</td>
                    <td>Default DatePicker Locale</td>
                  </tr>
                </tbody>
              </table>
              <h5>Sizes:</h5>
              <ul>
                <li>big</li>
                <li>medium</li>
                <li>small</li>
              </ul>
            </div>,
            examples: [
              {
                title: "Header Right",
                code: `import { DatePickerHeader } from "react-multi-date-picker/plugins"
.
.
.
<DatePicker
  plugins={[
    <DatePickerHeader />
  ]}
/>`,
                jsx: <DatePicker
                  plugins={[
                    <DatePickerHeader />
                  ]}
                />
              },
              {
                title: "Persian Calendar",
                code: `import { DatePickerHeader } from "react-multi-date-picker/plugins"
.
.
.
<DatePicker
  calendar="persian"
  locale="fa"
  plugins={[
    <DatePickerHeader />,
  ]}
/>`,
                jsx: <DatePicker
                  calendar="persian"
                  locale="fa"
                  plugins={[
                    <DatePickerHeader />,
                  ]}
                />
              },
              {
                title: "English Calendar with Indian Header",
                code: `import { DatePickerHeader } from "react-multi-date-picker/plugins"
.
.
.
<DatePicker
  plugins={[
    <DatePickerHeader 
      locale="hi" 
      size="medium" 
    />,
  ]}
/>`,
                jsx: <DatePicker
                  plugins={[
                    <DatePickerHeader locale="hi" size="medium" />,
                  ]}
                />
              },
              {
                title: "Styling Header",
                code: `import { DatePickerHeader } from "react-multi-date-picker/plugins"
.
.
.
<DatePicker
  plugins={[
    <DatePickerHeader 
      position="top" 
      size="small" 
      style={{ backgroundColor: "red" }} 
    />
  ]}
/>`,
                jsx: <DatePicker
                  plugins={[
                    <DatePickerHeader position="top" size="small" style={{ backgroundColor: "red" }} />
                  ]}
                />
              }
            ]
          },
          {
            title: "Date Panel",
            description: <div>
              <h5>Availble Props</h5>
              <table>
                <thead>
                  <tr>
                    <th>Prop</th>
                    <th>Type</th>
                    <th>Default</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td>sort</td>
                    <td>String</td>
                    <td>""</td>
                  </tr>
                  <tr>
                    <td>eachDaysInRange</td>
                    <td>Boolean</td>
                    <td>false</td>
                  </tr>
                  <tr>
                    <td>onDateClicked</td>
                    <td>Function</td>
                    <td>undefined</td>
                  </tr>
                  <tr>
                    <td>removeButton</td>
                    <td>Boolean</td>
                    <td>true</td>
                  </tr>
                </tbody>
              </table>
              <h5>sort:</h5>
              <ul>
                <li>date</li>
                <li>color</li>
              </ul>
            </div>,
            examples: [{
              title: "Sort By Date",
              code: `import { DatePanel } from "react-multi-date-picker/plugins"    
const [value, setValue] = useState([
  new DateObject(),
  new DateObject().add(2, "days"),
  new DateObject().add(10, "days")
])
.
.
.
<DatePicker
  value={value}
  onChange={setValue}
  multiple
  plugins={[
    <DatePanel sort="date" />
  ]}
/>
  `,
              jsx: <DatePicker
                value={rangeValue}
                onChange={setRangeValue}
                multiple
                plugins={[
                  <DatePanel sort="date" />
                ]}
              />

            },
            {
              title: "Without Remove Button",
              code: `import { DatePanel } from "react-multi-date-picker/plugins"
const [value, setValue] = useState([])
.
.
.
<DatePicker
  value={value}
  onChange={setValue}
  plugins={[
    <DatePanel
      removeButton={false}
    />
  ]}
/>`,
              jsx: <DatePicker
                value={value1}
                onChange={setValue1}
                plugins={[
                  <DatePanel
                    removeButton={false}
                  />
                ]}
              />

            },
            {
              title: "Date Click Listener",
              code: `import { DatePanel } from "react-multi-date-picker/plugins"
const [value, setValue] = useState([])
.
.
.
<DatePicker
  value={value}
  onChange={setValue}
  plugins={[
    <DatePanel
      removeButton={false}
      onDateClicked={dateObject => {
        let mustDelete = window.confirm("are you sure you want to delete this item ?")

        if (mustDelete) setValue(
          value.filter(date => date !== dateObject)
        )
      }}
    />
  ]}
/>`,
              jsx: <DatePicker
                value={value2}
                onChange={setValue2}
                plugins={[
                  <DatePanel
                    removeButton={false}
                    onDateClicked={dateObject => {
                      let mustDelete = window.confirm("are you sure you want to delete this date ?")

                      if (mustDelete) {
                        setValue2(value2.filter(date => date !== dateObject))
                      }
                    }}
                  />,

                ]}
              />

            }
            ]
          },
          {
            title: "Multi Colors",
            description: <div>
              <h5>Availble Props</h5>
              <table>
                <thead>
                  <tr>
                    <th>Prop</th>
                    <th>Type</th>
                    <th>Default</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td>colors</td>
                    <td>Array</td>
                    <td>["blue", "red", "green", "yellow"]</td>
                  </tr>
                  <tr>
                    <td>defaultColor</td>
                    <td>String</td>
                    <td>first item of colors</td>
                  </tr>
                  <tr>
                    <td>setProps</td>
                    <td>Function</td>
                    <td>undefined</td>
                  </tr>
                </tbody>
              </table>
            </div>,
            examples: [
              {
                title: "Default Color",
                code: `import { MultiColors } from "react-multi-date-picker/plugins"    
const [props, setProps] = useState({ multiple: true })
.
.
.
<DatePicker
  {...props}
  plugins={[
    <MultiColors 
      position="bottom" 
      defaultColor="green"
      setProps={setProps} 
    />
  ]}
/>
                                `,
                jsx: <DatePicker
                  {...props2}
                  plugins={[
                    <MultiColors
                      position="bottom"
                      defaultColor="green"
                      setProps={setProps2}
                    />
                  ]}
                />
              },
              {
                title: "With DatePanel",
                code: `import { MultiColors, DatePanel } from "react-multi-date-picker/plugins"   

const yesterday = new DateObject().subtract(1, "day")
const today = new DateObject()
const tomorrow = new DateObject().add(1, "day")
              
yesterday.color = "red"
today.color = "blue"
tomorrow.color = "red"
              
const initialProps = { 
  multiple: true, 
  value: [yesterday, today, tomorrow] 
}

const [props, setProps] = useState(initialProps) 
.
.
.
<DatePicker
  {...props}
  plugins={[
    <MultiColors
      position="bottom"
      colors={["blue", "red"]}
      setProps={setProps}
    />,
    <DatePanel sort="color" />
  ]}
/>`,
                jsx: <DatePicker
                  {...props3}
                  plugins={[
                    <MultiColors
                      position="bottom"
                      colors={["blue", "red"]}
                      setProps={setProps3}
                    />,
                    <DatePanel sort="color" />
                  ]}
                />
              }
            ]
          },
          {
            title: "Settings",
            description: <div>
              <h5>Availble Props</h5>
              <table>
                <thead>
                  <tr>
                    <th>Prop</th>
                    <th>Type</th>
                    <th>Default</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td>calendars</td>
                    <td>Array</td>
                    <td>["gregorian", "persian", "arabic", "indian"]</td>
                  </tr>
                  <tr>
                    <td>locales</td>
                    <td>Array</td>
                    <td>["en", "fa", "ar", "hi"]</td>
                  </tr>
                  <tr>
                    <td>modes</td>
                    <td>Array</td>
                    <td>["single", "multiple", "range"]</td>
                  </tr>
                  <tr>
                    <td>others</td>
                    <td>Array</td>
                    <td>["time picker", "only time picker", "only month picker", "only year picker"]</td>
                  </tr>
                  <tr>
                    <td>defaultActive</td>
                    <td>string</td>
                    <td>""</td>
                  </tr>
                  <tr>
                    <td>disabledList</td>
                    <td>Array</td>
                    <td>[]</td>
                  </tr>
                  <tr>
                    <td>defaultFormat</td>
                    <td>Object</td>
                    <td>{"{}"}</td>
                  </tr>
                  <tr>
                    <td>setProps</td>
                    <td>Function</td>
                    <td>undefined</td>
                  </tr>
                </tbody>
              </table>
            </div>,
            examples: [
              {
                title: "Settings Bottom",
                description: <div>
                </div>,
                code: `import { Settings } from "react-multi-date-picker/plugins"
const [props, setProps] = useState({})
.
.
.
<DatePicker
  {...props}
  plugins={[
    <Settings
      position="bottom"
      defaultActive="mode"
      setProps={setProps}
    />,
    <DatePanel 
      disabled={!props.multiple && !props.range} 
    />
  ]}
/>`,
                jsx: <DatePicker
                  {...settings1}
                  plugins={[
                    <Settings
                      position="bottom"
                      defaultActive="mode"
                      setProps={setSettings1}
                    />,
                    <DatePanel disabled={!settings1.multiple && !settings1.range} />
                  ]}
                />
              }, {
                title: "Custom Settings",
                code: `import { Settings } from "react-multi-date-picker/plugins"
const [props, setProps] = useState({ multiple: true })
.
.
.
<DatePicker
  {...props}
  plugins={[
    <Settings
      position="left"
      calendars={["gregorian", "persian"]}
      locales={["en", "fa"]}
      modes={["multiple", "range"]}
      disabledList={["other"]}
      setProps={setProps}
    />
  ]}
/>`,
                jsx: <DatePicker
                  {...settings2}
                  plugins={[
                    <Settings
                      position="left"
                      calendars={["gregorian", "persian"]}
                      locales={["en", "fa"]}
                      modes={["multiple", "range"]}
                      disabledList={["others"]}
                      setProps={setSettings2}
                    />
                  ]}
                />
              },
              {
                title: "Time Picker",
                code: `import { Settings } from "react-multi-date-picker/plugins"
const initialProps = { 
  value: new Date(), 
  format: "MM-DD-YYYY HH:mm:ss", 
  timePicker: true 
}
const [props, setProps] = useState(initialProps)
.
.
.
<DatePicker
  {...props}
  plugins={[
    <Settings
      position="bottom"
      disabledList={["calendar", "locale", "mode"]}
      others={["time picker", "only time picker"]}
      defaultActive="others"
      defaultFormat={{
        single: "MMM DD YYYY",
        timePicker: "MM-DD-YYYY HH:mm:ss",
        onlyTimePicker: "hh, mm, ss, a"
      }}
      setProps={setProps}
    />
  ]}
/>`,
                jsx: <DatePicker
                  {...settings3}
                  plugins={[
                    <Settings
                      position="bottom"
                      disabledList={["calendar", "locale", "mode"]}
                      others={["time picker", "only time picker"]}
                      defaultActive="others"
                      defaultFormat={{
                        single: "MMM DD YYYY",
                        timePicker: "MM-DD-YYYY HH:mm:ss",
                        onlyTimePicker: "hh, mm, ss, a"
                      }}
                      setProps={setSettings3}
                    />
                  ]}
                />
              }
            ]
          },
          {
            title: "Weekends",
            description: <div>
              <h5>Availble Props:</h5>
              <table>
                <thead>
                  <tr>
                    <th>Prop</th>
                    <th>Type</th>
                    <th>Default</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td>weekends</td>
                    <td>Array</td>
                    <td>*see default weekends</td>
                  </tr>
                  <tr>
                    <td>setMapDays</td>
                    <td>Function</td>
                    <td>undefined</td>
                  </tr>
                </tbody>
              </table>
              <h5>Default Weekends:</h5>
              <table>
                <thead>
                  <tr>
                    <th>Calendar</th>
                    <th>Weekends</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td>gregorian</td>
                    <td>[0, 6]</td>
                  </tr>
                  <tr>
                    <td>persian</td>
                    <td>[6]</td>
                  </tr>
                  <tr>
                    <td>arabic</td>
                    <td>[0, 6]</td>
                  </tr>
                  <tr>
                    <td>indian</td>
                    <td>[0]</td>
                  </tr>
                </tbody>
              </table>
            </div>,
            examples: [
              {
                title: "Weekends: gregorian",
                code: `import { Weekends } from "react-multi-date-picker/plugins" 
const [value, setValue] = useState()
const [mapDays, setMapDays] = useState()
.
.
.
<DatePicker
  value={value}
  onChange={setValue}
  mapDays={mapDays}
  plugins={[
    <Weekends setMapDays={setMapDays} />
  ]}
/>
            `,
                jsx: <DatePicker
                  mapDays={mapDays}
                  plugins={[
                    <Weekends setMapDays={setMapDays} />
                  ]}
                />
              },
              {
                title: "Weekends: persian",
                code: `import { Weekends } from "react-multi-date-picker/plugins" 
const [value, setValue] = useState()
const [mapDays, setMapDays] = useState()
.
.
.
<DatePicker
  value={value}
  onChange={setValue}
  mapDays={mapDays}
  plugins={[
    <Weekends setMapDays={setMapDays} />
  ]}
/>
                `,
                jsx: <DatePicker
                  calendar="persian"
                  locale="fa"
                  mapDays={mapDays}
                  plugins={[
                    <Weekends setMapDays={setMapDays} />
                  ]}
                />
              },
              {
                title: "Weekends: custom",
                code: `import { Weekends } from "react-multi-date-picker/plugins" 
const [value, setValue] = useState()
const [mapDays, setMapDays] = useState()
.
.
.
<DatePicker
  value={value}
  onChange={setValue}
  mapDays={mapDays}
  plugins={[
    <Weekends
      weekends={[5, 6]}
      setMapDays={setMapDays}
    />
  ]}
/>
                `,
                jsx: <DatePicker
                  mapDays={mapDays}
                  plugins={[
                    <Weekends
                      weekends={[5, 6]}
                      setMapDays={setMapDays}
                    />
                  ]}
                />
              }
            ]
          },
          {
            title: "Toolbar",
            code: `import { Toolbar } from "react-multi-date-picker/plugins"
const [value, setValue] = useState()
.
.
.
<DatePicker
  value={value}
  onChange={setValue}
  plugins={[
    <Toolbar position="bottom" />
  ]}
/>`,
            jsx: <DatePicker
              value={value}
              onChange={setValue}
              plugins={[
                <Toolbar position="bottom" />
              ]}
            />
          }
        ]
      }
    ]
  }

  // Doc = undefined

  let a = 0, b = 0

  function getExamples(examples) {
    if (!examples) return null

    return examples.map((example, index) => {
      a++
      return (
        <div key={a + index}>
          <Example
            id={example.title.replace(/\s/g, "-").toLowerCase()}
            title={example.title}
            description={example.description}
            code={example.code}
            jsx={example.jsx}
          />
          {getExamples(example.examples)}
        </div>
      )
    })
  }

  function getSidebarItem(examples) {
    if (!examples) return null

    return examples.map((example, index) => {
      b++
      return (
        <li key={b + index}>
          <a href={"#" + example.title.replace(/\s/g, "-").toLowerCase()}>{example.title}</a>
          <ul className="sidebar-sub-menu">
            {getSidebarItem(example.examples)}
          </ul>
        </li>
      )
    })
  }

  return (
    <div ref={appRef} className="App" >
      <nav className="navbar">
        <span className="open-sidebar" onClick={toggleSidebar}>&#9776;</span>
        <span className="title">
          {Doc?.navbar?.title}
        </span>
        <span>
          {" " + packageJson.dependencies["react-multi-date-picker"].replace("^", "")}
        </span>
        <span style={{ marginLeft: "10px" }}><Link to="/react-multi-date-picker">Home</Link></span>
        <div className="nav-images">
          {Doc ?
            Doc.navbar.logos.map((logo, index) => <div key={index} className="nav-image">
              <a target="_blank" rel="noopener noreferrer" className={logo.name} href={logo.url}>{logos[logo.name]}</a>
            </div>)
            : null
          }
        </div>
      </nav>
      <ul ref={sidebarRef} className={`sidebar ${isActive ? "active" : ""}`}>
        {Doc ? getSidebarItem(Doc.examples) : null}
      </ul>
      <div className="main">
        <div className="scroll-to-top"><Arrow direction="rmdp-up" /></div>
        {Doc && getExamples(Doc.examples)}
      </div>
    </div >
  );

  function toggleSidebar() {
    setIsActive(!isActive)
  }
}

export default Plugin;
