          code: `const [dateObject, setDateObject] = useState({ })
const [gregorian, setGregorian] = useState("")
const [persian, setPersian] = useState("")
const [arabic, setArabic] = useState("")
const [indian, setIndian] = useState("")
const [format, setFormat] = useState("MM/DD/YYYY")
const [jsDate, setJsDate] = useState("")

useEffect(() => {
  if (dateObject.isValid) {
    setGregorian(dateObject.convert("gregorian").format(format))
    setPersian(dateObject.convert("persian").format(format))
    setArabic(dateObject.convert("arabic").format(format))
    setIndian(dateObject.convert("indian").format(format))
    setJsDate(dateObject.toDate().toString())
  } else {
    setGregorian("")
    setPersian("")
    setArabic("")
    setIndian("")
    setJsDate("")
  }
}, [dateObject, format])

return(
  <div>
    <div>
      <div>
        <span style={{ fontWeight: "bold" }}>click to select:</span>
        <DatePicker
          onChange={dateObject => setDateObject(dateObject)}
        />
      </div>
      <div>
        <span style={{ fontWeight: "bold" }}>format:</span>
        <select
          value={format}
          onChange={e => setFormat(e.target.value)}
        >
          <option>MM/DD/YYYY</option>
          <option>DD-MM-YYYY</option>
          <option>YYYY,MM,DD</option>
          <option>dddd DD MMMM YYYY</option>
          <option>MMM/DD/YYYY hh:mm:ss a</option>
          <option>MMM/DD/YYYY HH:mm:ss</option>
        </select>
      </div>
      <div>
        <span style={{ fontWeight: "bold" }}>gregorian :</span>
        <span>{gregorian}</span>
      </div>
      <div>
        <span style={{ fontWeight: "bold" }}>persian :</span>
        <span>{persian}</span>
      </div>
      <div>
        <span style={{ fontWeight: "bold" }}>arabic :</span>
        <span>{arabic}</span>
      </div>
      <div>
        <span style={{ fontWeight: "bold" }}>indian :</span>
        <span>{indian}</span>
      </div>
      <div>
        <span style={{ fontWeight: "bold" }}>javascript date :</span>
        <span>{jsDate}</span>
      </div>
    </div>
  </div>
)
`,
